






























































import goodsCard from '../../../template/goods_card'
import loadMore from 'template/load_more.vue'
import naData from 'template/no_data'
import loading from 'template/loading'
export default {
	components: {
		goodsCard,
		loadMore,
		naData,
		loading
	},
	data() {
		return {
			currentTab: 0,
			tabs: [{
				name: '申请售后',
				url: '/api/aftersale/getEnableApply'
			}, {
				name: '处理中',
				url: '/api/aftersale/getApplying'
			}, {
				name: '申请记录',
				url: '/api/aftersale/getList'
			}],
			upDate:false,
			page_num: 1,
			page_size: 5,
			list: [],
			nodata: false,
			nodataText: "还没有相关订单哦~",
			loadingType: 0,
			company: [],
			writeNo: false,
			deliver_index: 0,
			deliveryNo: 0,
			afteId: 0,
			isLoading: false,
		};
	},
	onLoad() {
		this.showLoading()
		this.getList()
		this.getDeliveryCompany()
	},
	onShow() {
		if (this.upDate) {
			this.list = []
			this.getList()
		}
	},
	onPullDownRefresh() {
		this.loadingType = 0
		setTimeout(() => {
			this.list = []
			this.page_num = 1
			this.getList()
			uni.stopPullDownRefresh()
		}, 800);
	},
	onReachBottom() {
		if (this.loadingType !== 0) {
			return;
		}
		this.loadingType = 1;
		var page_num = this.page_num
		this.page_num = this.page_num + 1
		console.log(this.page_num)
		this.getList()

	},
	methods: {
		tabChange: function(index) {
			this.currentTab = index
			this.loadingType = 0;
			this.page_num = 1
			this.list = []
			this.showLoading()
			this.getList()
		},
		showLoading() {
			this.isLoading = true
			setTimeout(() => {
				this.isLoading = false
			}, 800);
		},

		getList() {
			var that = this
			this.nodata = false
			this.$api.get(this.tabs[this.currentTab].url, {
				page_num: this.page_num,
				page_size: this.page_size,
			}, function(res) {
				console.log(res)
				if (res.data.code == 200) {
					var oldList = that.list
					var list = res.data.data
					setTimeout(function(res) {
						if (oldList.length == 0) {
							that.loadingType = 0;
							if (list.length <= 0) {
								that.nodata = true
							} else {
								that.list = list
							}
						} else {
							if (list.length <= 0) {
								that.loadingType = 2;
							} else {
								that.loadingType = 0;
								that.list = oldList.concat(list)
							}
						}
					}, 800)
				}
			})
		},
		applyAfter(enable_apply, id) {
			if (enable_apply) {
				uni.navigateTo({
					url: 'apply/apply?id=' + id
				})
			} else {
				uni.showModal({
					title: '',
					content: '请先联系客服后再操作哦',
					showCancel:false,
					success: function(res) {
						if (res.confirm) {
							console.log('用户点击确定');
						} else if (res.cancel) {
							console.log('用户点击取消');
						}
					}
				});
			}
		},

		//操作
		operate(item, index, op) {
			console.log(item)
			var that = this
			this.afteId = item.id
			if (op == 'cancelApply') {
				uni.showModal({
					title: '确认取消申请吗?',
					confirmColor: '#e9101f',
					success: function(res) {
						if (res.confirm) {
							that.sendOP(item.id, index, op)
						}
					}
				})
			}
			if (op == 'writeDeliverNo') {
				that.writeNo = true

			}
			if (op == 'getDeliveryInfo') {
				uni.navigateTo({
					url: '../orders/logistics/logistics?from=1&id=' + item.id
				})
			}
		},

		//获取物流公司列表
		getDeliveryCompany: function() {
			var that = this;
			this.$api.get('/api/freight/getDeliveryCompany', null, function(res) {
				if (res.data.code == 200) {
					console.log(res)
					that.company = res.data.data
				} else {
					uni.showToast({
						title: res.data.msg,
						icon: "none"
					})
				}
			})
		},

		//选择器
		bindPickerChange: function(e) {
			this.deliver_index = e.detail.value
		},

		//获取输入的物流单号
		getDeliveryNo(e) {
			this.deliveryNo = e.detail.value
		},

		//取消填写物流单号
		cancelWriteNo() {
			this.deliver_index = 0
			this.writeNo = false
		},

		//发送操作请求
		sendOP(id, index, op) {
			var that = this
			this.$api.get('/api/aftersale/cancelApply', {
				aftersale_id: id
			}, function(res) {
				console.log(res)
				if (res.data.code == 200) {
					uni.showToast({
						title: "操作成功"
					})
					that.list = []
					that.page_num = 1
					that.getList()
				} else {
					uni.showToast({
						title: res.data.msg,
						icon: 'none'
					})
				}
			})
		},

		//提交售后物流信息
		submitWriteNo() {
			var that = this
			this.$api.post('/api/aftersale/writeDeliverNo', {
				aftersale_id: this.afteId,
				delivery_no: this.deliveryNo,
				delivery_company_id: this.company[this.deliver_index].id
			}, function(res) {
				console.log(res)
				if (res.data.code == 200) {
					uni.showToast({
						title: "操作成功"
					})
					that.writeNo = false
					that.list = []
					that.getList()
				} else {
					uni.showToast({
						title: res.data.msg,
						icon: 'none'
					})
				}
			})
		}
	}
}
